import * as React from 'react';
import { StyleSheet, css } from 'aphrodite';
import classnames from 'classnames';

import {
  BaseWidgetOptions,
  DisplayTypeDesktop,
  DisplayTypeMobile,
} from '../../WidgetOptions';
import { Modal, InputProps, Bar, Accordion, ButtonTrigger, MobileButtonTrigger, ModalControled, BarControled } from '../components';
import { mediaBreakPointsCss, fontSizes, button, styles } from '../../styles';
import { getPlatform, isOneOf } from '../../lib';
import { WidgetRenderTemplate, WidgetRenderTemplateState } from '../WidgetRenderTemplate';
import { Icon } from '../../icons';
import { lighten } from '../../lib';
import { createClasses, Classes } from '../createClasses';

export interface PromoWidgetOptions extends BaseWidgetOptions {
  contentOptions: PromoWidgetContentOptions;
}

export interface PromoWidgetContentOptions {
  title: string;
  description: string;
  buttonText: string;
  redirectUrl: string;
  triggerButtonText: string;
}

interface PromoState extends WidgetRenderTemplateState {
  modalOpen: boolean;
  open: boolean;
}

export class Promo extends WidgetRenderTemplate<PromoWidgetOptions, PromoState> {
  private classes: Classes;
  private styles: any;
  readonly name = 'promo';

  constructor(props: PromoWidgetOptions) {
    super(props);

    const {
      appearanceOptions,
    } = props;
    const {
      colors,
      font,
    } = appearanceOptions;

    const { medium, small } = mediaBreakPointsCss;

    this.classes = createClasses(props);
    this.styles = StyleSheet.create({
      modal: {
        maxWidth: '30em',
      },
      barHeader: {
        margin: '0 50px 0 10px',
      },
      actionButton: {
        ...button,
        backgroundColor: colors.button,
        color: colors.buttonText,
        fontSize: fontSizes.normal,
        padding: '.5em 1em',
        marginBottom: '1em',
        ':hover': {
          backgroundColor: lighten(colors.button, 20),
        },
      },
    });

    this.state = {
      ...this.state,
      modalOpen: false,
      open: true,
    };
  }

  protected renderModal = () => {
    const { appearanceOptions, contentOptions, displayTypeOptions } = this.props;
    const { displayTypeDesktop, displayTypeMobile, mobileButton } = displayTypeOptions;
    const { colors } = appearanceOptions;
    const {
      title,
      description,
      buttonText,
    } = contentOptions;

    const desktop = getPlatform() === 'desktop';
    const mobile = getPlatform() === 'mobile';

    const instant = desktop && !isOneOf<DisplayTypeDesktop>(displayTypeDesktop, [
      'buttonLeft', 'buttonRight',
    ]) || mobile && !isOneOf<DisplayTypeMobile>(displayTypeMobile, [
      'modalTriggered',
    ]);

    let trigger;
    if (!instant) {
      if (desktop && isOneOf<DisplayTypeDesktop>(displayTypeDesktop, ['buttonLeft', 'buttonRight'])) {
        const position = displayTypeDesktop === 'buttonLeft' ? 'left' : 'right';
        trigger = (
          <ButtonTrigger
            className={ this.classes.standaloneModalTrigger }
            backgroundColor={ colors.button }
            position={ position }
            icon='bullhorn'
          />
        );
      } else {
        trigger = (
          <MobileButtonTrigger
            className={ this.classes.standaloneModalTrigger }
            value='click me'
            color={ colors.buttonText }
            backgroundColor={ colors.button }
            position={ mobileButton }
          />
        );
      }
    }

    return (
      <Modal
        className={ classnames(this.classes.container, css(this.styles.modal)) }
        backgroundColor={ colors.background }
        animation={ appearanceOptions.animation }
        closeIconColor={ colors.text }
        trigger={ trigger }
      >
        <h2 className={ this.classes.dialogHeader }>{ title }</h2>
        <p className={ this.classes.dialogDescription }>{ description }</p>
        <button className={ css(this.styles.actionButton) } onClick={ this.handleActionButtonClick }>
          { buttonText }
        </button>
      </Modal>
    );
  }

  /** Desktop only yet. */
  protected renderBar = () => {
    const { displayTypeOptions, appearanceOptions, contentOptions, integrationsOptions } = this.props;
    const { modalOpen, open } = this.state;

    const { displayTypeDesktop } = displayTypeOptions;
    const { colors } = appearanceOptions;
    const { title, description, triggerButtonText, buttonText } = contentOptions;

    const barPosition = displayTypeDesktop === 'barTop' ? 'top' : 'bottom';

    return (
      <>
        <BarControled
          className={ this.classes.container }
          position={ barPosition }
          animation={ appearanceOptions.animation }
          closeIconColor={ colors.text }
          open={ open }
          onClose={ this.close }
        >
          <h2 className={ classnames(this.classes.barHeader, css(this.styles.barHeader)) }>{ title }</h2>
          <button className={ this.classes.barModalTrigger } onClick={ this.handleModalOpen }>{ triggerButtonText }</button>
        </BarControled>
        <ModalControled
          className={ classnames(this.classes.container, css(this.styles.modal)) }
          backgroundColor={ colors.background }
          animation={ appearanceOptions.animation }
          closeIconColor={ colors.text }
          open={ modalOpen }
          onClose={ this.handleModalClose }
        >
          <h2 className={ this.classes.dialogHeader }>{ title }</h2>
          <p className={ this.classes.dialogDescription }>{ description }</p>
          <button className={ css(this.styles.actionButton) } onClick={ this.handleActionButtonClick }>
            { buttonText }
          </button>
        </ModalControled>
      </>
    );
  }

  // desktop only yet
  protected renderAccordion = () => {
    const { appearanceOptions, displayTypeOptions, contentOptions, integrationsOptions } = this.props;
    const { displayTypeDesktop } = displayTypeOptions;
    const { title, description, buttonText } = contentOptions;
    const { colors } = appearanceOptions;

    const position = displayTypeDesktop === 'accordionLeft' ? 'left' : 'right';

    return (
      <Accordion
        className={ this.classes.container }
        position={ position }
        title={ title }
        titleColor={ colors.headerText }
        backgroundColor={ colors.background }
        containerSize='medium'
        dividerColor={ colors.text }
        contentClassName={ this.classes.accordionContent }
      >
        <p className={ this.classes.dialogDescription }>{ description }</p>
        <button className={ css(this.styles.actionButton) } onClick={ this.handleActionButtonClick }>
          { buttonText }
        </button>
      </Accordion>
    );
  }

  private handleActionButtonClick = () => {
    const { contentOptions } = this.props;

    window.open(contentOptions.redirectUrl, '_blank');
  }

  private handleModalOpen = () => {
    this.setState({ modalOpen: true });
  }

  private close = () => {
    this.setState({ open: false });
  }

  private handleModalClose = () => {
    this.setState({ modalOpen: false }, () => this.close());
  }
}
