import * as React from 'react';
import { StyleSheet, css } from 'aphrodite';

import { Styles, CSSClassConfig, styles } from '../styles';
import { BaseWidgetOptions } from '../WidgetOptions';

export interface DynamicStyles extends Styles {
  container: CSSClassConfig;
  successDialogTitle: CSSClassConfig;
  successDialogDescription: CSSClassConfig;
}

export type Classes = {
  [ P in (keyof Styles | keyof DynamicStyles) ]: string;
};

export const createClasses = (props: BaseWidgetOptions): Classes => {
  const {
    appearanceOptions,
  } = props;
  const {
    colors,
    font,
  } = appearanceOptions;

  const dynamicStyles = StyleSheet.create({
    container: {
      color: colors.text,
      fontFamily: font,
    },
    dialogDescription: {
      //
    },
    barDescription: {
      //
    },
    dialogHeader: {
      color: colors.headerText,
    },
    barHeader: {
      color: colors.headerText,
    },
    dialogForm: {
      //
    },
    barForm: {
      //
    },
    standaloneModalTrigger: {
      fontFamily: font,
      color: colors.buttonText,
    },
    barModalTrigger: {
      backgroundColor: colors.button,
      color: colors.buttonText,
    },
    accordionContent: {
      //
    },
    successDialogTitle: {
      color: colors.headerText,
      fontFamily: font,
    },
    successDialogDescription: {
      color: colors.text,
      fontFamily: font,
    },
  });

  return {
    container: css(dynamicStyles.container),
    dialogDescription: css(styles.dialogDescription, dynamicStyles.dialogDescription),
    barHeader: css(styles.barHeader, dynamicStyles.barHeader),
    barDescription: css(styles.barDescription, dynamicStyles.barDescription),
    dialogHeader: css(styles.dialogHeader, dynamicStyles.dialogHeader),
    dialogForm: css(styles.dialogForm, dynamicStyles.dialogForm),
    barForm: css(styles.barForm, dynamicStyles.barForm),
    standaloneModalTrigger: css(styles.standaloneModalTrigger, dynamicStyles.standaloneModalTrigger),
    barModalTrigger: css(styles.barModalTrigger, dynamicStyles.barModalTrigger),
    accordionContent: css(styles.accordionContent, dynamicStyles.accordionContent),
    successDialogTitle: css(dynamicStyles.successDialogTitle),
    successDialogDescription: css(dynamicStyles.successDialogDescription),
  };
};
