import * as React from 'react';

import {
  BaseWidgetOptions,
  DisplayTypeDesktop,
  SuccessPageOptions,
} from '../WidgetOptions';
import { getPlatform, isOneOf } from '../lib';

export interface WidgetRenderTemplateState {
  renderSuccessModal: boolean;
  display: boolean;
}

export abstract class WidgetRenderTemplate<P extends BaseWidgetOptions, S extends WidgetRenderTemplateState = WidgetRenderTemplateState> extends React.Component<P, S> {
  constructor(props: P) {
    super(props);

    this.state = {
      ...this.state,
      renderSuccessModal: false,
      display: true,
    };
  }

  render() {
    if (!this.state.display) {
      return null;
    }
    const { displayTypeOptions } = this.props;
    const {
      displayTypeDesktop,
      displayTypeMobile,
    } = displayTypeOptions;
    const { renderSuccessModal } = this.state;

    if (renderSuccessModal) {
      return this.renderSuccessModal();
    }

    ///////////////////////////// Desktop ////////////////////////////////////////////

    if (getPlatform() === 'desktop') {
      if (isOneOf<DisplayTypeDesktop>(displayTypeDesktop, [
        'bottomLeftModal', 'bottomRightModal', 'centerModal', 'buttonLeft', 'buttonRight',
      ])) {
        return this.renderModal();
      }

      if (isOneOf<DisplayTypeDesktop>(displayTypeDesktop, [
        'accordionLeft', 'accordionRight',
      ])) {
        return this.renderAccordion();
      }

      if (isOneOf<DisplayTypeDesktop>(displayTypeDesktop, [
        'barBottom', 'barTop',
      ])) {
        return this.renderBar();
      }
    }

    ///////////////////////////// Mobile ////////////////////////////////////////////

    // only modal variant for mobile yet
    return this.renderModal();
  }

  /** Renders mobile and desktop modal. */
  protected abstract renderModal(): React.ReactElement | null;

  /** Desktop only yet. */
  protected abstract renderBar (): React.ReactElement | null;

  /** Desktop only yet. */
  protected abstract renderAccordion(): React.ReactElement | null;

  /** Widgets that have success page must overwrite this method. */
  protected renderSuccessModal = (): React.ReactElement | null => {
    return null;
  }

  /** Triggers success modal rendering. Subclasses must not overwrite this method. */
  protected displaySuccessModal = (): void => {
    const { successPageOptions = {} as SuccessPageOptions } = this.props;
    if (successPageOptions.title || successPageOptions.description) {
      this.setState({ renderSuccessModal: true });
    }
  }

  protected hide = () => {
    this.setState({ display: false });
  }

  protected show = () => {
    this.setState({ display: true });
  }
}
