import * as React from 'react';
import { StyleSheet, css } from 'aphrodite';
import classnames from 'classnames';

import {
  BaseWidgetOptions,
  SuccessPageOptions,
  IntegrationsOptions,
  DisplayTypeDesktop,
  DisplayTypeMobile,
} from '../../WidgetOptions';
import { Modal, InputProps, Bar, Accordion, ButtonTrigger, MobileButtonTrigger } from '../components';
import { mediaBreakPointsCss, fontSizes, styles } from '../../styles';
import { SubscribeForm } from './SubscribeForm';
import { getPlatform, isOneOf } from '../../lib';
import { WidgetRenderTemplate, WidgetRenderTemplateState } from '../WidgetRenderTemplate';
import { createClasses, Classes } from '../createClasses';
import {
  subscribeFormAccordionStyles,
  subscribeFormBarStyles,
  subscribeFormModalStyles,
} from './styles';

export interface SubscribeWidgetOptions extends BaseWidgetOptions {
  contentOptions: SubscribeWidgetContentOptions;

  integrationsOptions: IntegrationsOptions;
  successPageOptions: SuccessPageOptions;
}

export interface SubscribeWidgetContentOptions {
  title: string;
  description: string;
  inputFields: SubscribeWidgetInputField[];
  buttonText: string;
  note?: string;
}

export interface SubscribeWidgetInputField extends InputProps {}

interface SubscribeState extends WidgetRenderTemplateState {}

export class Subscribe extends WidgetRenderTemplate<SubscribeWidgetOptions, SubscribeState> {
  private styles: any;
  private subscribeFormModalStyles: any;
  private subscribeFormAccordionStyles: any;
  private subscribeFormBarStyles: any;
  private classes: Classes;
  private readonly name = 'subscribe';

  constructor(props: SubscribeWidgetOptions) {
    super(props);

    const {
      appearanceOptions,
    } = props;
    const {
      colors,
      font,
    } = appearanceOptions;

    const { medium, small } = mediaBreakPointsCss;

    this.classes = createClasses(props);
    this.styles = StyleSheet.create({
      modal: {
        maxWidth: '100vw',
      },
      barHeader: {
        margin: '0 10px 0 10px',
      },
      dialogForm: {
        flexDirection: 'row',
        [ medium ]: {
          flexDirection: 'column',
        },
      },
      note: {
        fontSize: fontSizes.small,
        fontWeight: 'bold',
        color: colors.secondaryText,
      },
    });

    this.subscribeFormAccordionStyles = StyleSheet.create(subscribeFormAccordionStyles({ colors }) as {});
    this.subscribeFormBarStyles = StyleSheet.create(subscribeFormBarStyles({ colors }) as {});
    this.subscribeFormModalStyles = StyleSheet.create(subscribeFormModalStyles({ colors }) as {});
  }

  protected renderModal = () => {
    const { appearanceOptions, contentOptions, integrationsOptions, displayTypeOptions } = this.props;
    const { displayTypeDesktop, displayTypeMobile, mobileButton } = displayTypeOptions;
    const { colors } = appearanceOptions;
    const {
      title,
      description,
      note,
    } = contentOptions;
    const formAction = integrationsOptions.webhooks[0].url;

    const desktop = getPlatform() === 'desktop';
    const mobile = getPlatform() === 'mobile';

    const instant = desktop && !isOneOf<DisplayTypeDesktop>(displayTypeDesktop, [
      'buttonLeft', 'buttonRight',
    ]) || mobile && !isOneOf<DisplayTypeMobile>(displayTypeMobile, [
      'modalTriggered',
    ]);

    let trigger;
    if (!instant) {
      if (desktop && isOneOf<DisplayTypeDesktop>(displayTypeDesktop, ['buttonLeft', 'buttonRight'])) {
        const position = displayTypeDesktop === 'buttonLeft' ? 'left' : 'right';
        trigger = (
          <ButtonTrigger
            className={ this.classes.standaloneModalTrigger }
            backgroundColor={ colors.button }
            position={ position }
            icon='envelope'
          />
        );
      } else {
        trigger = (
          <MobileButtonTrigger
            className={ this.classes.standaloneModalTrigger }
            value='click me'
            color={ colors.buttonText }
            backgroundColor={ colors.button }
            position={ mobileButton }
          />
        );
      }
    }

    return (
      <Modal
        className={ classnames(this.classes.container, css(this.styles.modal)) }
        backgroundColor={ colors.background }
        animation={ appearanceOptions.animation }
        closeIconColor={ colors.text }
        trigger={ trigger }
      >
        <h2 className={ this.classes.dialogHeader }>{ title }</h2>
        <p className={ this.classes.dialogDescription }>{ description }</p>
        <SubscribeForm
          className={ classnames(this.classes.dialogForm, css(this.styles.dialogForm)) }
          action={ formAction }
          method='post' widgetOptions={ this.props }
          name={ this.name }
          styleSheet={ this.subscribeFormModalStyles }
        />
        { note && <p className={ css(this.styles.note) }>{ note }</p> }
      </Modal>
    );
  }

  /** Desktop only yet. */
  protected renderBar = (): React.ReactElement => {
    const { displayTypeOptions, appearanceOptions, contentOptions, integrationsOptions } = this.props;
    const { displayTypeDesktop } = displayTypeOptions;
    const { colors } = appearanceOptions;
    const { title, description } = contentOptions;
    const formAction = integrationsOptions.webhooks[0].url;

    const barPosition = displayTypeDesktop === 'barTop' ? 'top' : 'bottom';

    return (
      <Bar
        className={ this.classes.container }
        position={ barPosition }
        animation={ appearanceOptions.animation }
        closeIconColor={ colors.text }
      >
        <h2 className={ classnames(this.classes.barHeader, css(this.styles.barHeader)) }>{ title }</h2>
        <SubscribeForm
          className={ this.classes.barForm }
          action={ formAction }
          method='post'
          name={ this.name }
          widgetOptions={ this.props }
          styleSheet={ this.subscribeFormBarStyles }
        />
      </Bar>
    );
  }

  // desktop only yet
  protected renderAccordion = (): React.ReactElement => {
    const { appearanceOptions, displayTypeOptions, contentOptions, integrationsOptions } = this.props;
    const { displayTypeDesktop } = displayTypeOptions;
    const { title, description, note } = contentOptions;
    const { colors } = appearanceOptions;
    const formAction = integrationsOptions.webhooks[0].url;

    const position = displayTypeDesktop === 'accordionLeft' ? 'left' : 'right';

    return (
      <Accordion
        className={ this.classes.container }
        position={ position }
        title={ title }
        titleColor={ colors.headerText }
        backgroundColor={ colors.background }
        containerSize='medium'
        dividerColor={ colors.text }
      >
        <p className={ this.classes.dialogDescription }>{ description }</p>
        <SubscribeForm
          className={ classnames(this.classes.dialogForm, css(this.styles.dialogForm)) }
          action={ formAction }
          method='post'
          widgetOptions={ this.props }
          name={ this.name }
          styleSheet={ this.subscribeFormAccordionStyles }
        />
        { note && <p className={ css(this.styles.note) }>{ note }</p> }
      </Accordion>
    );
  }
}
