import * as React from 'react';
import { css, StyleSheet } from 'aphrodite';
import classnames from 'classnames';

import { SiteWidgetsWindow } from '../entryPoints/types';
import { join } from '../lib';

declare const window: SiteWidgetsWindow;

const baseFontsUrl = join(window.__siteWidgetsContext.baseUrl, 'fonts');

const IconFont = {
  fontFamily: 'IconFont',
  fontStyle: 'normal',
  fontWeight: 'normal',
  src: [
    `url("${ join(baseFontsUrl, 'IconFont.woff') }") format("woff")`,
    `url("${ join(baseFontsUrl, 'IconFont.ttf') }") format("trueType")`,
  ].join(', '),
};

const styles = StyleSheet.create({
  icon: {
    fontFamily: [ IconFont ],
    display: 'inline-block',
    width: '1em',
    height: '1em',
  },
});

export type IconName =
  | 'envelope'
  | 'chevronUp'
  | 'paperPlane'
  | 'externalLink'
  | 'bullhorn'
  | 'cross'
  | 'facebook'
  | 'twitter'
  | 'instagram'
  | 'youtube'
  ;

export type Icons = { [K in IconName]: string };

export const ICONS: Icons  = {
  cross: '\ue000',
  envelope: '\ue001',
  paperPlane: '\ue002',
  bullhorn: '\ue003',
  externalLink: '\ue004',
  facebook: '\ue005',
  instagram: '\ue006',
  twitter: '\ue007',
  youtube: '\ue008',
  chevronUp: '\ue009',
};

export interface IconProps {
  [key: string]: any;
  name: IconName;
}

export const Icon = (props: IconProps) => {
  const { name, className } = props;

  return <span { ...props } className={ classnames(className, css(styles.icon)) }>{ ICONS[name] }</span>;
};
