import * as React from 'react';
import { css, StyleSheet } from 'aphrodite';
import classnames from 'classnames';
import xor from 'lodash/xor';

import { FormMethod, ElementProps } from '../components';
import { createId, capitalize, lighten, isOneOf } from '../../lib';
import { formControls, button } from '../../styles';
import { Colors } from '../../WidgetOptions';

export type QuestionType =
  | 'open'
  | 'oneOf'
  | 'oneOfMultiple'
  ;

export interface Question {
  type: QuestionType;
  name: string;
  question: string;
  required?: boolean;
  options?: QuestionOption[];
}

export interface QuestionOption {
  name: string;
  value: string;
}

export interface SurveyPage {
  questions: Question[];
}

export interface SurveyFormProps extends ElementProps {
  pages: SurveyPage[];
  actionUrl: string;
  method: FormMethod;
  colors: Colors;
  onClose: () => void;
  onSubmit: () => void;
  submitButtonText: string;
  nextPageButtonText: string;
}

interface SurveyFormState {
  [ key: string ]: string | string[] | number | boolean;
  currentPageNumber: number;
  errors: string[];
  renderSuccessModal: boolean;
}

const styles = StyleSheet.create({
  container: {
    display: 'flex',
    flexDirection: 'column',
  },
  page: {
    //
  },
  pageCount: {
    display: 'flex',
    justifyContent: 'center',
  },
  // @ts-ignore
  question: {
    display: 'flex',
    flexDirection: 'column',
    margin: '2em 1px',
  },
  // @ts-ignore
  input: {
    ...formControls,
    margin: '1em 1px 0 1px',
  },
  checkboxInput: {
    cursor: 'pointer',
    flex: '0 1 auto',
    margin: '0 1.5em',
    order: 1,
  },
  radioInput: {
    cursor: 'pointer',
    flex: '0 1 auto',
    margin: '0 1.5em',
    order: 1,
  },
  inputContainer: {
    display: 'flex',
    marginTop: '.5em',
    borderRadius: '2px',
  },
  inputLabel: {
    cursor: 'pointer',
    display: 'flex',
    alignItems: 'center',
    order: 2,
  },
  errorInput: {
    boxShadow: '0 0 0 1px red',
  },
  // @ts-ignore
  submitButton: {
    ...formControls,
    ...button,
  },
});

export class SurveyForm extends React.Component<SurveyFormProps, SurveyFormState> {
  private styles: any;
  private questionIds: { [key: string]: string };

  constructor (props: SurveyFormProps) {
    super(props);

    const { pages, colors } = props;

    this.questionIds = {};

    // init field values as default empty strings
    const allQuestions = {} as { [key: string]: string | string[] };
    pages.forEach((page) => page.questions.forEach((question) => {
      allQuestions[question.name] = question.type === 'oneOfMultiple' ? [] as string[] : '';
      this.questionIds[question.name] = createId();
    }));

    this.state = {
      currentPageNumber: 0,
      errors: [],
      renderSuccessModal: false,
      ...allQuestions,
    };

    this.styles = StyleSheet.create({
      button: {
        backgroundColor: colors.button,
        color: colors.buttonText,
        ':hover': {
          backgroundColor: lighten(colors.button, 100),
        },
      },
    });
  }

  private handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.currentTarget) {
      const { name, value, type } = e.currentTarget;
      if (type === 'checkbox') {
        const values = xor(this.state[name] as string[], [ value ]);
        this.setState({ [name]: values });
      } else {
        this.setState({ [name]: value });
      }
    }
  }

  render() {
    const {
      pages,
      className,
      actionUrl,
      method,
      colors,
      nextPageButtonText,
      submitButtonText,
    } = this.props;
    const {
      currentPageNumber,
      errors,
      renderSuccessModal,
      ...values
    } = this.state;

    const currentPage = (
      <Page
        colors={ colors }
        questions={ pages[currentPageNumber].questions }
        pageNumber={ currentPageNumber }
        totalPages={ pages.length }
        ids={ this.questionIds }
        values={ values }
        onChange={ this.handleChange }
        errors={ errors }
      />
    );

    const hasNext = currentPageNumber < pages.length - 1;

    return (
      <form
        onSubmit={ this.handleSubmit }
        className={ classnames(className, css(styles.container)) }
        action={ actionUrl }
        method={ method }
      >
        { currentPage }
        <input
          className={ css(styles.submitButton, this.styles.button) }
          type='button'
          onClick={ hasNext ? this.handleNextPageBtnClick : this.handleSubmit }
          value={ hasNext ? nextPageButtonText : submitButtonText }
        />
      </form>
    );
  }

  private validatePage = (): boolean => {
    const { pages } = this.props;
    const { currentPageNumber } = this.state;
    const errors = [] as string[];

    pages[currentPageNumber].questions.forEach((question) => {
      const currentValue = this.state[question.name] as string | string[];

      if (question.required && isEmptyField(currentValue)) {
        errors.push(question.name);
      }
    });

    this.setState({ errors });

    return errors.length === 0;
  }

  private handleNextPageBtnClick = () => {
    if (!this.validatePage()) {
      return;
    }
    const { currentPageNumber } = this.state;
    const { pages } = this.props;

    if (currentPageNumber < pages.length - 1) {
      this.setState((prev) => {
        return { currentPageNumber: prev.currentPageNumber + 1 };
      });
    }
  }

  private handleSubmit = (e: React.SyntheticEvent) => {
    e.preventDefault();
    if (!this.validatePage()) {
      return;
    }

    const {
      onSubmit,
    } = this.props;

    onSubmit();
  }
}

interface PageProps extends SurveyPage {
  colors: Colors;
  pageNumber: number;
  totalPages: number;
  /** { questionName: currnetValue, ... }. */
  values: any;
  /** { questionName: id, ... }. */
  ids: any;
  onChange: (e: React.ChangeEvent<HTMLInputElement>) => void;
  errors: string[];
  className?: string;
}

const Page = (props: PageProps) => {
  const { pageNumber, totalPages, questions, values, onChange, ids, colors, errors } = props;

  return (
    <div className={ css(styles.page) }>
      { totalPages > 1 && <div className={ css(styles.pageCount) }>{ pageNumber + 1 } / { totalPages }</div> }
      <div>
        {
          questions.map((question) => {
            return (
              <QuestionInput
                colors={ colors }
                id={ ids[question.name] }
                key={ ids[question.name] }
                question={ question.question }
                type={ question.type }
                name={ question.name }
                required={ question.required }
                options={ question.options }
                onChange={ onChange }
                value={ values[question.name] }
                error={ isOneOf(question.name, errors) }
              />
            );
        })
        }
      </div>
    </div>
  );
};

interface QuestionInputProps extends Question {
  colors: Colors;
  onChange: (e: React.ChangeEvent<HTMLInputElement>) => void;
  name: string;
  value: string | string[];
  id: string;
  error?: boolean;
  className?: string;
}

const QuestionInput = (props: QuestionInputProps) => {
  const {
    type,
    question,
    required,
    options,
    onChange,
    name,
    value,
    id,
    colors,
    error,
  } = props;

  const questyonStyles = StyleSheet.create({
    inputOption: {
      backgroundColor: colors.button,
      color: colors.buttonText,
    },
  });

  if ((type === 'oneOf' || type === 'oneOfMultiple') && options) {
    return (
      <div className={ classnames(css(styles.question), { [css(styles.errorInput)]: error }) }>
        <header>{ (required ? '* ' : '') + capitalize(question) }</header>
        {
          options.map((opt, i) => {
            return (
              <div key={ id + opt.name } className={ css(styles.inputContainer, questyonStyles.inputOption) }>
                <label htmlFor={ id + opt.name } className={ css(styles.inputLabel) }>{ opt.value }</label>
                <input
                  className={ css(styles.input, type === 'oneOf' ? styles.radioInput : styles.checkboxInput) }
                  id={ id + opt.name }
                  type={ type === 'oneOf' ? 'radio' : 'checkbox' }
                  name={ name }
                  value={ opt.value }
                  checked={ typeof value === 'string' ? opt.value === value : value.indexOf(opt.value) !== -1 }
                  onChange={ onChange }
                />
              </div>
            );
          })
        }
      </div>
    );
  }

  return (
    <div className={ classnames(css(styles.question, styles.inputContainer), { [css(styles.errorInput)]: error }) }>
      <header>{ (required ? '* ' : '') + capitalize(question) }</header>
      <input
        className={ css(styles.input) }
        autoComplete='off'
        id={ id }
        type='text'
        name={ name }
        value={ value }
        onChange={ onChange }
        required={ required }
      />
    </div>
  );
};

const isEmptyField = (value: string | string[]): boolean => {
  return !value || value.length === 0;
};
