import * as React from 'react';
import { css } from 'aphrodite';
import * as _ from 'lodash';

import { SurveyWidgetOptions, Survey } from '../src/widgets/Survey';
import { exclude } from '../src/lib';

import {
  SurveyWidgetContentOptions,
} from '../src/widgets';
import {
  DisplayTypeOptions,
  AppearanceOptions,
  BehaviorOptions,
  TargetingOptions,
  SuccessPageOptions,
  IntegrationsOptions,
  Colors,
  DisplayTypeDesktop,
  DisplayTypeMobile,
  Animation,
  FinalAction,
} from '../src/WidgetOptions';

interface SurveyConfigState {
  displayTypeDesktop: DisplayTypeDesktop;
  displayTypeMobile: DisplayTypeMobile;
  backgroundColor: string;
  headerTextColor: string;
  textColor: string;
  secondaryTextColor: string;
  buttonColor: string;
  buttonTextColor: string;
  font: string;
  animation: Animation;
  title: string;
  description: string;
  buttonText: string;
  nameInput: boolean;
  triggerButtonText: string;
  submitButtonText: string;
  nextPageButtonText: string;
  successTitle: string;
  successDescription: string;
  successRedirectUrl: string;
  successFinalAction: FinalAction;

  displayWidget: boolean;
}

export interface SurveyConfigFormProps {
  styles: any;
}

export class SurveyConfigForm extends React.Component<SurveyConfigFormProps, SurveyConfigState> {

  constructor(props: SurveyConfigFormProps) {
    super(props);

    this.state = {
      displayTypeDesktop: 'centerModal',
      displayTypeMobile: 'modalTriggered',
      backgroundColor: '#191B1F',
      headerTextColor: '#FFFFFF',
      textColor: '#bec5c6',
      secondaryTextColor: '#efce4a',
      buttonColor: '#5A5F6B',
      buttonTextColor: '#100C1C',
      font: '"Arial", sans-serif',
      animation: 'slideInOut',
      title: 'this is title',
      description: 'corem ipsum dolor sit amet, consectetur adipiscing elit. Phasellus id ipsum sed nunc congue lobortis ut eu mi. Donec ut felis viverra, mollis magna blandit, iaculis odio. Ut mattis ut tortor in molestie. Nullam euismod placerat dapibus. Aliquam erat volutpat.',
      buttonText: 'send',
      nameInput: true,
      triggerButtonText: 'write a message',
      submitButtonText: 'Send',
      nextPageButtonText: 'Next',
      successTitle: 'Thanks for your answers',
      successDescription: 'Enjoy you time',
      successRedirectUrl: 'https://en.wikipedia.org/wiki/Main_Page',
      successFinalAction: 'successDialog',

      displayWidget: false,
    };
  }

  handleChange = (e: React.SyntheticEvent) => {
    if (e.target) {
      // @ts-ignore
      const name = e.target.name;
      // @ts-ignore
      const value = e.target.type === 'checkbox' ? e.target.checked : e.target.value;
      // @ts-ignore
      this.setState({ [name]: value });
    }
  }

  render() {
    const { styles } = this.props;
    const {
      displayTypeDesktop,
      displayTypeMobile,
      backgroundColor,
      headerTextColor,
      textColor,
      secondaryTextColor,
      buttonColor,
      buttonTextColor,
      font,
      animation,
      title,
      description,
      buttonText,
      nameInput,
      triggerButtonText,
      submitButtonText,
      nextPageButtonText,
      successTitle,
      successDescription,
      successRedirectUrl,
      successFinalAction,

      displayWidget,
    } = this.state;

    const config: SurveyConfigState = exclude(this.state, [ 'displayWidget' ]);

    const displayTypeDesktopOptions = [
      { name: 'centerModal', value: 'centerModal' },
      { name: 'buttonRight', value: 'buttonRight' },
      { name: 'buttonLeft', value: 'buttonLeft' },
      { name: 'barTop', value: 'barTop' },
      { name: 'barBottom', value: 'barBottom' },
      { name: 'accordionLeft', value: 'accordionLeft' },
      { name: 'accordionRight', value: 'accordionRight' },
    ];

    const displayTypeMobileOptions = [
      { name: 'modal', value: 'modal' },
      { name: 'modalTriggered', value: 'modalTriggered' },
    ];

    const fontOptions = [
      { name: 'Arial', value: '"Arial", sans-serif' },
      { name: 'Helvetica', value: '"Helvetica", sans-serif' },
      { name: 'Times New Roman', value: '"Times New Roman", serif' },
      { name: 'Times', value: '"Times", serif' },
      { name: 'Courier', value: '"Courier", serif' },
      { name: 'Verdana', value: '"Courier", sans-serif' },
      { name: 'Georgia', value: '"Georgia", serif' },
    ];

    const successFinalActionOptions = [
      { name: 'redirect', value: 'redirect' },
      { name: 'close', value: 'close' },
      { name: 'successDialog', value: 'successDialog' },
    ];

    const animationOptions = [
      { name: 'fadeInOut', value: 'fadeInOut' },
      { name: 'bounce', value: 'bounce' },
      { name: 'slideInOut', value: 'slideInOut' },
      { name: 'none', value: 'none' },
    ];

    return (
      <>
      <form className={ css(styles.form) } onSubmit={ this.onSubmit }>
        <label className={ css(styles.label) }>
          displayTypeDesktop:
          <select className={ css(styles.input) } name='displayTypeDesktop' onChange={ this.handleChange } value={ displayTypeDesktop }>
            { displayTypeDesktopOptions.map(opt => <option value={ opt.value } key={ opt.value }>{ opt.name }</option>) }
          </select>
        </label>
        <label className={ css(styles.label) }>
          displayTypeMobile
          <select className={ css(styles.input) } name='displayTypeMobile' onChange={ this.handleChange } value={ displayTypeMobile }>
            { displayTypeMobileOptions.map(opt => <option value={ opt.value } key={ opt.value }>{ opt.name }</option>) }
          </select>
        </label>
        <label className={ css(styles.label) }>
          backgroundColor
          <input type='color' className={ css(styles.input) } name='backgroundColor' onChange={ this.handleChange } value={ backgroundColor } />
        </label>
        <label className={ css(styles.label) }>
          headerTextColor
          <input  type='color' className={ css(styles.input) } name='headerTextColor' onChange={ this.handleChange } value={ headerTextColor } />
        </label>
        <label className={ css(styles.label) }>
          textColor
          <input  type='color' className={ css(styles.input) } name='textColor' onChange={ this.handleChange } value={ textColor } />
        </label>
        <label className={ css(styles.label) }>
          secondaryTextColor
          <input  type='color' className={ css(styles.input) } name='secondaryTextColor' onChange={ this.handleChange } value={ secondaryTextColor } />
        </label>
        <label className={ css(styles.label) }>
          buttonColor
          <input  type='color' className={ css(styles.input) } name='buttonColor' onChange={ this.handleChange } value={ buttonColor } />
        </label>
        <label className={ css(styles.label) }>
          buttonTextColor
          <input  type='color' className={ css(styles.input) } name='buttonTextColor' onChange={ this.handleChange } value={ buttonTextColor } />
        </label>
        <label className={ css(styles.label) }>
          font
          <select className={ css(styles.input) } name='font' onChange={ this.handleChange } value={ font }>
            { fontOptions.map(opt => <option value={ opt.value } key={ opt.value }>{ opt.name }</option>) }
          </select>
        </label>
        <label className={ css(styles.label) }>
          animation
          <select className={ css(styles.input) } name='animation' onChange={ this.handleChange } value={ animation }>
            { animationOptions.map(opt => <option value={ opt.value } key={ opt.value }>{ opt.name }</option>) }
          </select>
        </label>
        <label className={ css(styles.label) }>
          title
          <input className={ css(styles.input) } name='title' onChange={ this.handleChange } value={ title } />
        </label>
        <label className={ css(styles.label) }>
          description
          <input className={ css(styles.input) } name='description' onChange={ this.handleChange } value={ description } />
        </label>
        <label className={ css(styles.label) }>
          buttonText
          <input className={ css(styles.input) } name='buttonText' onChange={ this.handleChange } value={ buttonText } />
        </label>
        <label className={ css(styles.label) }>
          triggerButtonText
          <input className={ css(styles.input) } name='triggerButtonText' onChange={ this.handleChange } value={ triggerButtonText } />
        </label>
        <label className={ css(styles.label) }>
          nextPageButtonText
          <input className={ css(styles.input) } name='nextPageButtonText' onChange={ this.handleChange } value={ nextPageButtonText } />
        </label>
        <label className={ css(styles.label) }>
          submitButtonText
          <input className={ css(styles.input) } name='submitButtonText' onChange={ this.handleChange } value={ submitButtonText } />
        </label>
        <label className={ css(styles.label) }>
          successTitle
          <input className={ css(styles.input) } name='successTitle' onChange={ this.handleChange } value={ successTitle } />
        </label>
        <label className={ css(styles.label) }>
          successDescription
          <input className={ css(styles.input) } name='successDescription' onChange={ this.handleChange } value={ successDescription } />
        </label>
        <label className={ css(styles.label) }>
          successRedirectUrl
          <input className={ css(styles.input) } name='successRedirectUrl' onChange={ this.handleChange } value={ successRedirectUrl } />
        </label>
        <label className={ css(styles.label) }>
          successFinalAction
          <select className={ css(styles.input) } name='successFinalAction' onChange={ this.handleChange } value={ successFinalAction }>
            { successFinalActionOptions.map(opt => <option value={ opt.value } key={ opt.value }>{ opt.name }</option>) }
          </select>
        </label>
        <input className={ css(styles.input) } type='submit' name='displayTypeDesktop' value={ displayWidget ? 'reset' : 'apply' } />
      </form>
      { displayWidget && <Survey { ...createSurveyOptions(config) } /> }
      </>
    );
  }

  onSubmit = (e: React.SyntheticEvent) => {
    e.preventDefault();

    this.setState((prevState) => {
      return { displayWidget: !prevState.displayWidget };
    });
  }
}

const createSurveyOptions = (config: SurveyConfigState) => {
  const {
    displayTypeDesktop,
    displayTypeMobile,
    backgroundColor: background,
    headerTextColor: headerText,
    textColor: text,
    secondaryTextColor: secondaryText,
    buttonColor: button,
    buttonTextColor,
    font,
    animation,
    title,
    description,
    buttonText,
    nameInput,
    triggerButtonText,
    submitButtonText,
    nextPageButtonText,
    successTitle,
    successDescription,
    successRedirectUrl,
    successFinalAction,
  } = config;

  const contentOptions: SurveyWidgetContentOptions = {
    title,
    description,
    submitButtonText,
    nextPageButtonText,
    triggerButtonText,
    pages: [
      {
        questions: [
          { type: 'open', name: 'general', required: true, question: 'what\'s up?' },
          { type: 'oneOf', name: 'chooseOne', required: true, question: 'what\'s the most common element in Universe?', options: [
            { name: 'Silicon', value: 'Silicon' },
            { name: 'Hydrogen', value: 'Hydrogen' },
            { name: 'Gold', value: 'Gold' },
          ] },
          { type: 'oneOfMultiple', name: 'chooseMany', required: false, question: 'what is valid js, select all that appplies.', options: [
            { name: 'let x = 1;', value: 'let x = 1;' },
            { name: 'x >> 1;', value: 'x >> 1;' },
            { name: 'if x then y;', value: 'if x then y;' },
            { name: 'let abc; if (abc = void(0)) 123;', value: 'let abc; if (abc = void(0)) 123;' },
          ] },
        ],
      },
      {
        questions: [
          { type: 'open', name: 'anotherGeneral', required: true, question: 'how is it going?' },
          { type: 'oneOf', name: 'anotherChooseOne', required: true, question: 'How long does it take to sunlight to reach the Earth?', options: [
            { name: '2 minutes', value: '2 minutes' },
            { name: '4 minutes', value: '4 minutes' },
            { name: '8 minutes', value: '8 minutes' },
          ] },
        ],
      },
    ],
  };

  const displayTypeOptions: DisplayTypeOptions = {
    displayTypeDesktop,
    displayTypeMobile,
    mobileButton: 'right',
  };

  const colorTheme: Colors = {
    background,
    headerText,
    text,
    secondaryText,
    button,
    buttonText,
  };

  const appearanceOptions: AppearanceOptions = {
    colors: colorTheme,
    font,
    animation,
  };

  const behaviorOptions: BehaviorOptions = {
    startDisplay: {
      type: 'immediate',
    },
    frequency: {
      type: 'always',
    },
  };

  const targetingOptions: TargetingOptions = {
    path: '*',
    type: 'include',
    visitorRatio: 1,
  };

  const successPageOptions: SuccessPageOptions = {
    title: successTitle,
    description: successDescription,
    redirectUrl: successRedirectUrl,
    finalAction: successFinalAction,
  };

  const surveyOptions: SurveyWidgetOptions = {
    type: 'Survey',
    displayTypeOptions,
    appearanceOptions,
    contentOptions,
    behaviorOptions,
    targetingOptions,
    successPageOptions,
  };

  return surveyOptions;
};
