import * as React from 'react';
import { css, StyleSheet } from 'aphrodite';
import classnames from 'classnames';

import { BaseWidgetOptions, DisplayTypeDesktop, DisplayTypeMobile, SuccessPageOptions } from '../../WidgetOptions';
import { SurveyPage, SurveyForm } from './SurveyForm';
import { WidgetRenderTemplate, WidgetRenderTemplateState } from '../../widgets/WidgetRenderTemplate';
import { getPlatform, isOneOf, noop } from '../../lib';
import {
  ButtonTrigger,
  MobileButtonTrigger,
  Modal,
  ModalControled,
  BarControled,
  Accordion,
} from '../components';
import { createClasses, Classes } from '../createClasses';

export interface SurveyWidgetOptions extends BaseWidgetOptions {
  contentOptions: SurveyWidgetContentOptions;
}

export interface SurveyWidgetContentOptions {
  title: string;
  description: string;
  submitButtonText: string;
  nextPageButtonText: string;
  triggerButtonText: string;
  pages: SurveyPage[];
}

interface SurveyState extends WidgetRenderTemplateState {
  open: boolean;
  modalOpen: boolean;
}

export class Survey extends WidgetRenderTemplate<SurveyWidgetOptions, SurveyState> {
  private styles: any;
  private classes: Classes;

  constructor(props: SurveyWidgetOptions) {
    super(props);

    this.classes = createClasses(props);
    this.styles = StyleSheet.create({
      modal: {
        maxWidth: '30em',
      },
    });

    this.state = {
      ...this.state,
      open: true,
      modalOpen: false,
      renderSuccessModal: false,
    };
  }

  renderModal() {
    const { appearanceOptions, contentOptions, displayTypeOptions, successPageOptions } = this.props;
    const { displayTypeDesktop, displayTypeMobile, mobileButton } = displayTypeOptions;
    const { colors } = appearanceOptions;
    const {
      title,
      description,
      nextPageButtonText,
      submitButtonText,
      pages,
    } = contentOptions;
    const { open, renderSuccessModal } = this.state;

    const desktop = getPlatform() === 'desktop';
    const mobile = getPlatform() === 'mobile';

    const instant = desktop && !isOneOf<DisplayTypeDesktop>(displayTypeDesktop, [
      'buttonLeft', 'buttonRight',
    ]) || mobile && !isOneOf<DisplayTypeMobile>(displayTypeMobile, [
      'modalTriggered',
    ]);

    let trigger;
    if (!instant) {
      if (desktop && isOneOf<DisplayTypeDesktop>(displayTypeDesktop, ['buttonLeft', 'buttonRight'])) {
        const position = displayTypeDesktop === 'buttonLeft' ? 'left' : 'right';
        trigger = (
          <ButtonTrigger
            className={ this.classes.standaloneModalTrigger }
            backgroundColor={ colors.button }
            position={ position }
            icon='bullhorn'
          />
        );
      } else {
        trigger = (
          <MobileButtonTrigger
            className={ this.classes.standaloneModalTrigger }
            value='click me'
            color={ colors.buttonText }
            backgroundColor={ colors.button }
            position={ mobileButton }
          />
        );
      }
    }

    return (
      <ModalControled
        className={ classnames(this.classes.container, css(this.styles.modal)) }
        backgroundColor={ colors.background }
        animation={ appearanceOptions.animation }
        closeIconColor={ colors.text }
        trigger={ trigger }
        onClose={ this.close }
        open={ open }
      >
        <h2 className={ this.classes.dialogHeader }>{ title }</h2>
        <p className={ this.classes.dialogDescription }>{ description }</p>
        <SurveyForm
          actionUrl=''
          method='post'
          pages={ pages }
          colors={ colors }
          nextPageButtonText={ nextPageButtonText }
          submitButtonText={ submitButtonText }
          onClose={ this.close }
          onSubmit={ this.onSurveyFormSubmit }
        />
      </ModalControled>
    );
  }

  renderBar = () => {
    const { displayTypeOptions, appearanceOptions, contentOptions, integrationsOptions } = this.props;
    const { modalOpen, open } = this.state;

    const { displayTypeDesktop } = displayTypeOptions;
    const { colors } = appearanceOptions;
    const { title, description, triggerButtonText, submitButtonText, nextPageButtonText, pages } = contentOptions;

    const barPosition = displayTypeDesktop === 'barTop' ? 'top' : 'bottom';

    return (
      <>
        <BarControled
          className={ this.classes.container }
          position={ barPosition }
          animation={ appearanceOptions.animation }
          closeIconColor={ colors.text }
          open={ open }
          onClose={ this.close }
        >
          <h2 className={ classnames(this.classes.barHeader, css(this.styles.barHeader)) }>{ title }</h2>
          <button className={ this.classes.barModalTrigger } onClick={ this.handleModalOpen }>{ triggerButtonText }</button>
        </BarControled>
        <ModalControled
        className={ classnames(this.classes.container, css(this.styles.modal)) }
        backgroundColor={ colors.background }
        animation={ appearanceOptions.animation }
        closeIconColor={ colors.text }
        onClose={ this.close }
        open={ modalOpen }
      >
        <h2 className={ this.classes.dialogHeader }>{ title }</h2>
        <p className={ this.classes.dialogDescription }>{ description }</p>
        <SurveyForm
          actionUrl=''
          method='post'
          pages={ pages }
          colors={ colors }
          nextPageButtonText={ nextPageButtonText }
          submitButtonText={ submitButtonText }
          onClose={ this.close }
          onSubmit={ this.onSurveyFormSubmit }
        />
      </ModalControled>
      </>
    );
  }

  renderAccordion = () => {
    const { appearanceOptions, displayTypeOptions, contentOptions, integrationsOptions } = this.props;
    const { displayTypeDesktop } = displayTypeOptions;
    const { title, description, submitButtonText, nextPageButtonText, pages } = contentOptions;
    const { colors } = appearanceOptions;

    const position = displayTypeDesktop === 'accordionLeft' ? 'left' : 'right';

    return (
      <Accordion
        className={ this.classes.container }
        position={ position }
        title={ title }
        titleColor={ colors.headerText }
        backgroundColor={ colors.background }
        containerSize='medium'
        dividerColor={ colors.text }
        contentClassName={ this.classes.accordionContent }
      >
        <h2 className={ this.classes.dialogHeader }>{ title }</h2>
        <p className={ this.classes.dialogDescription }>{ description }</p>
        <SurveyForm
          actionUrl=''
          method='post'
          pages={ pages }
          colors={ colors }
          submitButtonText={ submitButtonText }
          nextPageButtonText={ nextPageButtonText }
          onClose={ this.close }
          onSubmit={ this.onSurveyFormSubmit }
        />
      </Accordion>
    );
  }

  private handleModalOpen = () => {
    this.setState({ modalOpen: true });
  }

  private close = () => {
    this.setState({ open: false, modalOpen: false, renderSuccessModal: false });
    this.hide();
  }

  private onSurveyFormSubmit = () => {
    const { successPageOptions = {} as SuccessPageOptions } = this.props;

    switch (successPageOptions.finalAction) {
      case 'redirect':
        if (successPageOptions.redirectUrl) {
          window.open(successPageOptions.redirectUrl, '_blank');
        }
        this.close();
        break;
      case 'successDialog':
        this.displaySuccessModal();
        break;
      default:
        this.close();
        break;
    }
  }

  protected renderSuccessModal = () => {
    const {
      successPageOptions = {} as SuccessPageOptions,
      appearanceOptions,
    } = this.props;
    const { colors } = appearanceOptions;
    const { renderSuccessModal } = this.state;

    if (renderSuccessModal && successPageOptions && successPageOptions.title) {
      return (
        <ModalControled
          open={ renderSuccessModal }
          onClose={ this.close || noop }
          backgroundColor={ colors.background }
          animation='none'
          closeIconColor={ colors.text }
        >
          <h2 className={ this.classes.successDialogTitle } >{ successPageOptions.title }</h2>
          {
            successPageOptions.description
            && <p className={ this.classes.successDialogDescription }>{ successPageOptions.description }</p>
          }
        </ModalControled>
      );
    }

    return null;
  }
}
