import * as React from 'react';
import { StyleSheet, css } from 'aphrodite';
import classnames from 'classnames';

import {
  BaseWidgetOptions,
  SuccessPageOptions,
  IntegrationsOptions,
  DisplayTypeDesktop,
  DisplayTypeMobile,
} from '../../WidgetOptions';
import { Modal, InputProps, Bar, Accordion, ButtonTrigger, MobileButtonTrigger, ModalControled, BarControled } from '../components';
import { mediaBreakPointsCss, fontSizes, styles } from '../../styles';
import { getPlatform, isOneOf } from '../../lib';
import { WidgetRenderTemplate, WidgetRenderTemplateState } from '../WidgetRenderTemplate';
import { ContactForm } from './ContactForm';
import { contactFormModalStyles, contactFormAccordionStyles } from './styles';
import { createClasses, Classes } from '../createClasses';

export interface ContactWidgetOptions extends BaseWidgetOptions {
  contentOptions: ContactWidgetContentOptions;

  integrationsOptions: IntegrationsOptions;
  successPageOptions: SuccessPageOptions;
}

export interface ContactWidgetContentOptions {
  title: string;
  description: string;
  inputFields: ContactWidgetInputField[];
  buttonText: string;
  triggerButtonText?: string;
}

export interface ContactWidgetInputField extends InputProps {}

interface ContactState extends WidgetRenderTemplateState {
  modalOpen: boolean;
  open: boolean;
}

export class Contact extends WidgetRenderTemplate<ContactWidgetOptions, ContactState> {
  private styles: any;
  private classes: Classes;
  private readonly name = 'contact';
  private contactFormModalStyles: any;
  private contactFormBarStyles: any;
  private contactFormAccordionStyles: any;

  constructor(props: ContactWidgetOptions) {
    super(props);

    const {
      appearanceOptions,
    } = props;
    const {
      colors,
      font,
    } = appearanceOptions;

    const { medium, small } = mediaBreakPointsCss;

    this.classes = createClasses(props);
    this.styles = StyleSheet.create({
      modal: {
        maxWidth: '30em',
      },
      barHeader: {
        margin: '0 50px 0 10px',
      },
    });

    this.contactFormModalStyles = StyleSheet.create(contactFormModalStyles({ colors }) as {});
    this.contactFormBarStyles = this.contactFormModalStyles;
    this.contactFormAccordionStyles = StyleSheet.create(contactFormAccordionStyles({ colors }) as {});

    this.state = {
      ...this.state,
      modalOpen: false,
      open: true,
    };
  }

  protected renderModal = () => {
    const { appearanceOptions, contentOptions, integrationsOptions, displayTypeOptions } = this.props;
    const { displayTypeDesktop, displayTypeMobile, mobileButton } = displayTypeOptions;
    const { colors } = appearanceOptions;
    const {
      title,
      description,
    } = contentOptions;
    const formAction = integrationsOptions.webhooks[0].url;

    const desktop = getPlatform() === 'desktop';
    const mobile = getPlatform() === 'mobile';

    const instant = desktop && !isOneOf<DisplayTypeDesktop>(displayTypeDesktop, [
      'buttonLeft', 'buttonRight',
    ]) || mobile && !isOneOf<DisplayTypeMobile>(displayTypeMobile, [
      'modalTriggered',
    ]);

    let trigger;
    if (!instant) {
      if (desktop && isOneOf<DisplayTypeDesktop>(displayTypeDesktop, ['buttonLeft', 'buttonRight'])) {
        const position = displayTypeDesktop === 'buttonLeft' ? 'left' : 'right';
        trigger = (
          <ButtonTrigger
            className={ this.classes.standaloneModalTrigger }
            backgroundColor={ colors.button }
            position={ position }
            icon='paperPlane'
          />
        );
      } else {
        trigger = (
          <MobileButtonTrigger
            className={ this.classes.standaloneModalTrigger }
            value='click me'
            color={ colors.buttonText }
            backgroundColor={ colors.button }
            position={ mobileButton }
          />
        );
      }
    }

    return (
      <Modal
        className={ classnames(this.classes.container, css(this.styles.modal)) }
        backgroundColor={ colors.background }
        animation={ appearanceOptions.animation }
        closeIconColor={ colors.text }
        trigger={ trigger }
      >
        <h2 className={ this.classes.dialogHeader }>{ title }</h2>
        <p className={ this.classes.dialogDescription }>{ description }</p>
        <ContactForm className={ this.classes.dialogForm } action={ formAction } method='post' widgetOptions={ this.props } name={ this.name } styleSheet={ this.contactFormModalStyles } />
      </Modal>
    );
  }

  /** Desktop only yet. */
  protected renderBar = () => {
    const { displayTypeOptions, appearanceOptions, contentOptions, integrationsOptions } = this.props;
    const { modalOpen, open } = this.state;

    const { displayTypeDesktop } = displayTypeOptions;
    const { colors } = appearanceOptions;
    const { title, description, triggerButtonText } = contentOptions;
    const formAction = integrationsOptions.webhooks[0].url;

    const barPosition = displayTypeDesktop === 'barTop' ? 'top' : 'bottom';

    return (
      <>
        <BarControled
          className={ this.classes.container }
          position={ barPosition }
          animation={ appearanceOptions.animation }
          closeIconColor={ colors.text }
          open={ open }
          onClose={ this.close }
        >
          <h2 className={ classnames(this.classes.barHeader, css(this.styles.barHeader)) }>{ title }</h2>
          <button className={ this.classes.barModalTrigger } onClick={ this.handleModalOpen }>{ triggerButtonText }</button>
        </BarControled>
        <ModalControled
          className={ classnames(this.classes.container, css(this.styles.modal)) }
          backgroundColor={ colors.background }
          animation={ appearanceOptions.animation }
          closeIconColor={ colors.text }
          open={ modalOpen }
          onClose={ this.handleModalClose }
        >
          <h2 className={ this.classes.dialogHeader }>{ title }</h2>
          <p className={ this.classes.dialogDescription }>{ description }</p>
          <ContactForm className={ this.classes.dialogForm } action={ formAction } method='post' widgetOptions={ this.props } name={ this.name } styleSheet={ this.contactFormBarStyles } />
        </ModalControled>
      </>
    );
  }

  // desktop only yet
  protected renderAccordion = () => {
    const { appearanceOptions, displayTypeOptions, contentOptions, integrationsOptions } = this.props;
    const { displayTypeDesktop } = displayTypeOptions;
    const { title, description } = contentOptions;
    const { colors } = appearanceOptions;
    const formAction = integrationsOptions.webhooks[0].url;

    const position = displayTypeDesktop === 'accordionLeft' ? 'left' : 'right';

    return (
      <Accordion
        className={ this.classes.container }
        position={ position }
        title={ title }
        titleColor={ colors.headerText }
        backgroundColor={ colors.background }
        containerSize='medium'
        dividerColor={ colors.text }
        contentClassName={ this.classes.accordionContent }
      >
        <p className={ this.classes.dialogDescription }>{ description }</p>
        <ContactForm className={ this.classes.dialogForm } action={ formAction } method='post' widgetOptions={ this.props } name={ this.name } styleSheet={ this.contactFormAccordionStyles } />
      </Accordion>
    );
  }

  private handleModalOpen = () => {
    this.setState({ modalOpen: true });
  }

  private close = () => {
    this.setState({ open: false });
  }

  private handleModalClose = () => {
    this.setState({ modalOpen: false }, () => this.close());
  }
}
